.footer {
  height: 10vh;
  text-align: center;
  background-color: #222;
  color: white;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  box-shadow: 0 0 3px 0 black;
}

.footer__name {
  font-size: 18px;
  line-height: 10vh;

  @media (min-width: 768px) {
    font-size: 25px;
  }

  @media (min-width: 1024px) {
    font-size: 30px;
  }
}
