@import "../../styles/mixins.scss";
@import "../../styles/variables.scss";

.errorPage {
  @include animation();
  position: relative;
  text-align: center;
}
.errorPage__img {
  width: 90%;
  border-radius: 15px;
  box-shadow: 0 0 4px black;
  margin: 30px 0;
}

.errorPage__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  width: 60%;
  height: 45%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.7);
}

.errorPage__text {
  padding: 10px 5px 5px;
  color: white;
  text-transform: uppercase;
  font-size: 22px;
  font-weight: bold;

  @media (min-width: 640px) and (orientation: landscape) {
    margin-top: 20px;
    font-size: 25px;
  }

  @media (min-width: 768px) {
    margin-top: 28px;
    font-size: 28px;
  }

  @media (min-width: 1024px) {
    font-size: 33px;
  }
}

.errorPage__backBtn {
  padding: 5px 15px;
  margin: 10px auto;
  font-size: 12px;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  font-family: "Roboto Condensed", sans-serif;
  background-color: transparent;
  border: 2px $main-color solid;

  @media (min-width: 640px) and (orientation: landscape) {
    margin-bottom: 20px;
    font-size: 16px;
  }

  @media (min-width: 768px) {
    margin-bottom: 28px;
    font-size: 19px;
  }

  @media (min-width: 1024px) {
    padding: 10px 20px;
    font-size: 23px;
    cursor: pointer;
    transition: 0.2s;
  }
}

@media (min-width: 1024px) {
  .errorPage__backBtn:hover {
    background-color: $main-color;
    border-radius: 15px;
  }
}
