@import "../../styles/mixins.scss";
@import "../../styles/variables.scss";

.experience {
  @include animation();
  display: flex;
  flex-direction: column;
  margin: 20px 0;
}

.experience__title {
  @include title();
}

.experience__wrapper {
  margin: 25px 15px;

  @media (min-width: 768px) and (orientation: portrait) {
    width: 50vw;
    padding: 15px;
    margin: 25px auto;
  }

  @media (min-width: 1024px) {
    width: 60vw;
    padding: 20px;
    margin: 25px auto;
  }
}

.experience__wrapper:nth-of-type(odd) {
  @media (min-width: 1024px) {
    margin: 25px 0 25px 30px;
  }
}

.experience__wrapper:nth-of-type(even) {
  @media (min-width: 1024px) {
    margin: 25px 30px 25px auto;
  }
}

.experience__companyName {
  padding: 10px;
  background-color: $main-color;
  color: white;
  font-size: 24px;
  box-shadow: 0 0 2px 0 gray;

  @media (min-width: 768px) {
    font-size: 29px;
  }

  @media (min-width: 1024px) {
    font-size: 33px;
  }
}

.experience__text {
  margin-top: 8px;
  padding: 6px 10px;
  font-size: 18px;
  text-align: justify;
  background-color: #eee;
  line-height: 150%;
  box-shadow: 0 0 2px 0 gray;

  @media (min-width: 768px) {
    font-size: 22px;
  }

  @media (min-width: 1024px) {
    font-size: 25px;
  }
}

.experience__text--italic {
  font-style: italic;
}

.experience__text--boldGolden {
  font-weight: bold;
  color: $main-color;
}

.experience__text--fz {
  font-size: 16px;

  @media (min-width: 768px) {
    font-size: 19px;
  }

  @media (min-width: 1024px) {
    font-size: 22px;
  }
}
